@keyframes lds-double-ring {
    0% {
        -webkit-transform: rotate(0);
        transform: rotate(0);
    }
    100% {
        -webkit-transform: rotate(360deg);
        transform: rotate(360deg);
    }
}
@-webkit-keyframes lds-double-ring {
    0% {
        -webkit-transform: rotate(0);
        transform: rotate(0);
    }
    100% {
        -webkit-transform: rotate(360deg);
        transform: rotate(360deg);
    }
}
@keyframes lds-double-ring_reverse {
    0% {
        -webkit-transform: rotate(0);
        transform: rotate(0);
    }
    100% {
        -webkit-transform: rotate(-360deg);
        transform: rotate(-360deg);
    }
}
@-webkit-keyframes lds-double-ring_reverse {
    0% {
        -webkit-transform: rotate(0);
        transform: rotate(0);
    }
    100% {
        -webkit-transform: rotate(-360deg);
        transform: rotate(-360deg);
    }
}
.spinner {
    width: 100%;
    margin-bottom: 10px;
}
.lds-double-ring div {
    box-sizing: border-box;
}
.lds-double-ring > div {
    position: absolute;
    width: 76px;
    height: 76px;
    top: 0;
    left: 0;
    border-radius: 50%;
    border: 4px solid #000;
    border-color: #2e7fb5 transparent #2e7fb5 transparent;
    -webkit-animation: lds-double-ring 1s linear infinite;
    animation: lds-double-ring 1s linear infinite;
}
.lds-double-ring > div:nth-child(2),
.lds-double-ring > div:nth-child(4) {
    width: 64px;
    height: 64px;
    top: 6px;
    left: 6px;
    -webkit-animation: lds-double-ring_reverse 1s linear infinite;
    animation: lds-double-ring_reverse 1s linear infinite;
}
.lds-double-ring > div:nth-child(2) {
    border-color: transparent #44c1d7 transparent #44c1d7;
}
.lds-double-ring > div:nth-child(3) {
    border-color: transparent;
}
.lds-double-ring > div:nth-child(3) div {
    position: absolute;
    width: 100%;
    height: 100%;
    -webkit-transform: rotate(45deg);
    transform: rotate(45deg);
}
.lds-double-ring > div:nth-child(3) div:before,
.lds-double-ring > div:nth-child(3) div:after {
    content: "";
    display: block;
    position: absolute;
    width: 4px;
    height: 4px;
    top: -4px;
    left: 32px;
    background: #2e7fb5;
    border-radius: 50%;
    box-shadow: 0 72px 0 0 #2e7fb5;
}
.lds-double-ring > div:nth-child(3) div:after {
    left: -4px;
    top: 32px;
    box-shadow: 72px 0 0 0 #2e7fb5;
}
.lds-double-ring > div:nth-child(4) {
    border-color: transparent;
}
.lds-double-ring > div:nth-child(4) div {
    position: absolute;
    width: 100%;
    height: 100%;
    -webkit-transform: rotate(45deg);
    transform: rotate(45deg);
}
.lds-double-ring > div:nth-child(4) div:before,
.lds-double-ring > div:nth-child(4) div:after {
    content: "";
    display: block;
    position: absolute;
    width: 4px;
    height: 4px;
    top: -4px;
    left: 26px;
    background: #44c1d7;
    border-radius: 50%;
    box-shadow: 0 60px 0 0 #44c1d7;
}
.lds-double-ring > div:nth-child(4) div:after {
    left: -4px;
    top: 26px;
    box-shadow: 60px 0 0 0 #44c1d7;
}
.lds-double-ring {
    position: relative;
    margin: 0 auto;
    //width: 200px !important;
    //height: 200px !important;
    width: 76px;
    height: 76px;
    -webkit-transform: translate(-100px, -100px) scale(1) translate(100px, 100px);
    transform: translate(-100px, -100px) scale(1) translate(100px, 100px);
}
